<template>
  <div class="sider">
    <div class="logo" >LOGO</div>
    <div class="menu">
      <div class="menu-item" v-for="item in Navs" >
        <router-link :to="item.router">
          <span>icon</span>
          {{item.title}}
          <span> ></span>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      Navs: [
        {title: '', router: '/photo', name: ''},
        {title: '', router: '/calcendar', name: ''},
        {title: '', router: '/echarts', name: ''},
        {title: '', router: '/subway', name: ''},
      ]
    }
  }
}
</script>

<style>
.sider{
  position: fixed;
  z-index: 99;
  top: 0px;
  width: 145px;
  height: 100%;
  background-color: rgb(40, 43, 47);
}
.sider .logo {
  width: 145px;
  height: 45px;
  line-height: 45px;
  font-size: 18px;
  font-weight: 400;
  /* padding-left: 20px; */
  color: #fff;
  background-color: rgb(29,29,38);
}
.sider .menu {
  color: aliceblue;
}
.menu-item {
  height: 38px;
  line-height: 38px;
  font-size: 12px;
  padding-left: 16px;
  border-bottom: 1px solid rgb(75, 75, 75);
}
.menu-item>a {
  display: block;
  color: #fff;
  font-size: 12px;
  text-decoration: none;
}
</style>



