import Vue from 'vue'
import Router from 'vue-router'
import OverView from '@/Pages/OverView'

import Photo from '@/Pages/Photo'
import Calendar from '@/Pages/Calendar'
import Subway from '@/Pages/Subway'

Vue.use(Router)
const Routes = [
  { path: '/photo', title: '', name: 'photo', component: Photo },
  { path: '/calcendar', title: '', name: 'calcendar', component: Calendar} ,
  { path: '/echarts', title: '', name: 'echarts'},
  { path: '/subway', title: '', name: 'subway', component: Subway},
]
export default new Router({
  routes: [
    {
      path: '/',
      name: 'OverView',
      component: OverView
    }, ...Routes
  ]
})



