<template>
  <div class="hello">
     <!-- <div>/:{{curYearMonths}}</div>
     <div>:{{unSelectDays}}</div> -->
      <input type="text" @click="openMultiByDrop($event)" v-model="calendar5.display" readonly >
      <transition name="fade">
        <div class="calendar-dropdown" :style="{'left':calendar5.left+'px','top':calendar5.top+'px'}" v-if="calendar5.show">
            <calendar 
                :zero="calendar5.zero" 
                :disabled="calendar5.disabled" 
                :lunar="calendar5.lunar" 
                :value="calendar5.value" 
                :multi="calendar5.multi" 
                @currentYearMon="calendar5.currentYearMon"
                @next="calendar5.nextMonth"
                @prev="calendar5.prevMonth"
                @select="calendar5.select"></calendar>
        </div>
      </transition>
  </div>
</template>

<script>
import calendar from './demo/monthCalendar.vue'
 
export default {
    props:['selectDate','UnselectDate'],
    components: {
        calendar
    },
    data(){
        return {
            curYearMonths:'',
            // 
            calendar5:{
                display:"",
                multi:true,
                show:false,
                zero:true,
                unSelectDays:[], //
                value:[[2019,7,8],[2019,7,9]], //
                disabled:[[2017,12,24],[2017,12,25]], //
                lunar:true, //
                select:(value)=>{
                    // console.log("value",value);
                    let displayValue=[]
                    if (value.length > 0) {
                        value.forEach(v=>{ 
                            displayValue.push(v[2])
                        })
                        // console.log(displayValue);
                        this.calendar5.display= value[0][0] +'-' + (value[0][1]>9 ? value[0][1]:'0'+ value[0][1]) + ':' + displayValue.join(",");
                    } else {
                        this.calendar5.display=''
                    }
                    // this.calendar5.show=false;
                    this.calendar5.value=value;
                    this.$emit("submitSelectValue",this.calendar5.value);
                },
                currentYearMon:(year,mon)=> {
                    let currSelectDays = this.calendar5.value.filter(v => {
                        return year===v[0]&&mon===v[1]
                    })
                    this.chooseReverse(year, mon, currSelectDays)
                },
                nextMonth: (mon,year)=>{
                    
                },
                prevMonth: (mon,year)=>{

                }
            },
        }
    },
    methods:{
        openMultiByDrop(e){
            this.calendar5.show=true;
            this.calendar5.left=e.target.offsetLeft;
            this.calendar5.top=e.target.offsetTop+50;
            e.stopPropagation();
            window.setTimeout(()=>{
                document.addEventListener("click",(e)=>{
                    this.calendar5.show=false;
                    document.removeEventListener("click",()=>{},false);
                },false);
            },1000)
        },
        //
        chooseReverse(year, mon, currSelectDays){
            // 
            let daysCount = new Date(year, mon, 0).getDate() 
            var fullMonth = []; 
            for(var i= 1; i <= daysCount; i++) {
                let _date = [year, mon, i]
                fullMonth.push(_date)
            }
            // 
            let reverse = []
            let _fullMonth = []

            fullMonth.map(v =>  _fullMonth.push(v[2]))
            currSelectDays.map(v => {
                _fullMonth.map(full => {
                    if (v[2] === full) {
                       let index = _fullMonth.indexOf(v[2])
                       _fullMonth.splice(index, 1)
                    } 
                })
            })
            _fullMonth.map(v => reverse.push([year, mon, v]) )
            if (reverse.length == 0){
                this.calendar5.unSelectDays = []; //
            } else {
                let unSelect = [];
                reverse.map(v => unSelect.push(v[2]))
                //   
                let _month =  reverse[0][1]>9 ? reverse[0][1]:'0'+ reverse[0][1];
                this.calendar5.unSelectDays = reverse[0][0] + '-' + _month +' :' + unSelect.join(',');
                //
                this.curYearMonths = reverse[0][0] + '' + reverse[0][1] + '';
                // console.log(' this.unSelectDays', this.unSelectDays);
                this.$emit("submitUnSelectValue",this.calendar5.unSelectDays);
            }
        }
    },
    created() {
        let year = new Date().getFullYear()
        let mon = new Date().getMonth() + 1
        let day = new Date().getDate()
        this.calendar5.value = [[year, mon, day]]
        this.calendar5.display = year + '-' + mon + ':' + day
        this.calendar5.currentYearMon(year, mon, this.calendar5.value)
    },
    watch:{
        selectDate(v) {
            // console.log('', v)
            this.calendar5.value = v
            if (v.length>0) {
                this.calendar5.display= v.map(item => {
                    return item[0]  + '-' + item[1] + ':' + item[2]
                }).join(',')
            } else {
                this.calendar5.display=''
            }
        },
        UnselectDate(val){
            this.calendar5.unSelectDays = val;
            console.log("val",val)
        }
    }

}
</script>

<style>
.hello input{
    box-sizing: border-box;
    display: inline-block;
    width:100%;
    height: 32px;
    line-height: 1.5;
    padding: 4px 7px;
    font-size: 12px;
    border: 1px solid #dcdee2;
    border-radius: 4px;
    color: #515a6e;
    background-color: #fff;
    cursor: text;
    background:url(data:image/svg+xml;base64,) no-repeat 220px 7px;
}
/*transition*/
.fade-enter-active,
.fade-leave-active {
    transition: all .5s ease-in-out;
}
.fade-enter,.fade-leave-active{
    opacity: 0;
    transform: translateY(-10px);

}
/**/
.calendar-dropdown{
    background: #fff;
    position: absolute;
    left:0;
    top:0;
    padding:10px;
    border: 1px solid #eee;
    border-radius: 4px;
}
.calendar-dropdown:before {
    position: absolute;
    left:30px;
    top: -10px;
    content: "";
    border:5px solid rgba(0, 0, 0, 0);
    border-bottom-color: #DEDEDE;
}
.calendar-dropdown:after {
    position: absolute;
    left:30px;
    top: -9px;
    content: "";
    border:5px solid rgba(0, 0, 0, 0);
    border-bottom-color: #fff;
}
/**/
.calendar-dialog{
    position: absolute;
    left:0;
    top:0;
    right:0;
    bottom:0;
}
.calendar-dialog-mask{
    background:rgba(255,255,255,.5);
    width:100%;
    height:100%;
}
.calendar-dialog-body{
    background: #fff;
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    padding:20px;
    border: 1px solid #eee;
    border-radius: 2px;
}
</style>


